<script>
import Avatar from "@/components/Avatar.vue";
import { mapState } from "vuex";
export default {
  name: "AppHeader",
  components: {
    Avatar,
  },
  data: () => ({
    userDropdown: false,
    notification: false,
    hasReadAll: false,
  }),
  computed: {
    ...mapState({
      lockedBranch: (state) => state.branch,
    }),
    notifications() {
      return this.loggedUser.alerts || [];
    },
    hasNotifications() {
      return (
        this.notifications.some((item) => item.read_at == null) &&
        !this.hasReadAll
      );
    },
  },
  methods: {
    showNotifications() {
      this.notification = !this.notification;
      if (this.hasNotifications) {
        this.$store.commit("SET_REQUEST_FLAG", "READING_NOTIFICATIONS");
        this.$store
          .dispatch("getRequest", "auth/notifications/read")
          .then(() => {
            this.hasReadAll = true;
          });
      }
    },
    notificationIcon(event) {
      var result;
      switch (event) {
        case "StockTransfer":
          result = "bg-success ni-building-fill";
          break;
        case "RequisitionUpdated":
          result = "bg-primary-dim ni-reload";
          break;
        default:
          result = "bg-primary ni ni-trend-up";
      }
      return result;
    },
    signout() {
      this.$store
        .dispatch("postRequest", {
          url: "auth/logout",
          formData: this.$helper.generateFormData({}),
        })
        .then((response) => {
          if (response.data.status) {
            this.$store.dispatch("logout").then(() => {
              window.location.replace("/front-office/");
            });
          }
        });
    },
  },
};
</script>
